

































































/* Transition */
.fade-transition {
  transition: opacity .22s ease;
}
.fade-enter,
.fade-leave {
  opacity: 0;
}
/* Theme */
.tm-row {
  padding-top: 15px;
}
.tm-cursor-default {
  cursor: default;
}
.tm-button-half {
  min-width: 45px !important;
}
.uk-button[disabled]:hover {
  position: static !important;
}
/* Horizontal rule */
.tm-row hr {
  margin: 0;
}
.tm-row:hover hr {
  border-color: #aaa;
}
/* Visibility toggle */
.uk-visible-hover-inline:hover .tm-visible-toggle {
  display: none !important;
  visibility: hidden !important;
}



